const userListStore = {
  namespaced: true,
  state: {
    list: {
      total: 0,
      page: 1,
      perPage: 10,
      data: [],
    },
    loaded: false
  },
  mutations: {
    MU_LOADED: (state, payload) => {
      state.loaded = payload.loaded
    },
    MU_SET_USERLIST: (state, payload) => {
      console.log('SAVELIST')
      state.list.data = payload.data;
      state.list.total = payload.total;
      state.list.perPage = payload.perPage;
      state.list.page = payload.page;
    }
  },
  getters: {
    GET_PAGE: state=>state.list.page,
    GET_DATA: state=>state.list.data,
    GET_PERPAGE: state=>state.list.perPage,
    GET_TOTAL: state=>state.list.total,
    GET_USERLIST: state=>state.list,
    GET_LOADED: state=>state.loaded,
  },
  actions: {
    AC_LOADED: ({commit}, payload) => {
      commit('MU_LOADED', payload)
    },
    AC_SET_USERLIST: ({commit}, payload) => {
      commit('MU_SET_USERLIST', payload)
    }
  }
}

export default userListStore;